.header {
  max-width: 130rem;
  margin: auto;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  background-color: #2e363f;
  text-align: center;
  font-family:sans-serif;
}

.list {
  display: flex;
  gap: 2.5rem;
}

.list a {
  text-decoration: none;
  color: var(--color-primary-400);
}

.list a:hover,
.list a.active {
  color: var(--color-primary-800);
}
/* Add any additional styles for the button as needed */
.logout-btn {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  color: inherit;
  cursor: pointer;
}
