/* Styles for the HomePage component */

/* Header styles */

header {
  text-align: center;
  padding: 2rem;
  background-color: #fff;
  color: #130101;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

button {
  background-color: #007bff;
  color: #fff;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Section styles */
.sec-1 {
  float: left;
  width: 50%;
  padding: 2rem;
  background-color: #fff;
  color: black;
  margin-bottom: 2rem;
}

.sec-2 {
  float: right;
  width: 50%;
  color: black;
  padding: 2rem;
  background-color: #fff;
}

h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

/* Main content container */
.content-container {
  flex-direction: column;
  background-color: #fff;
  min-height: 100vh;
}

/* Footer styles */
footer {
  text-align: center;
  padding-bottom: 0.3rem;
  background-color: #fff;
  font-size: 0.9rem;
  color: #130101;
  margin-top: auto;
}
