/* EventsList.module.css */
.event {
  max-width: 100rem;
  margin: 2rem auto;
  display: flex; /* Use Flexbox to align image and content side by side */
  align-items: center; /* Center content vertically */
  text-align: center;
  color: black;
}

.event img { 
  width: 60rem;
  border-radius: 5px;
  padding-left: 10px;
  margin-right: 10rem; /* Add margin to separate image from content */
}



.event-content {
  flex: 1; /* Allow content to take available width */
  text-align: left; /* Align content to the left side */
}



.actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.actions a,
.actions button {
  padding: 0.25rem 1rem;
  text-decoration: none;
  font: inherit;
  cursor: pointer;
}

.actions button {
  background-color: transparent;
  border: none;
  color: var(--color-primary-800);
}
